<template>
  <a-form-model class="login-form" :model="form" ref="ruleForm" :rules="rules">
    <a-form-model-item prop="username">
      <a-input  @keyup.enter="onSubmit" placeholder="用户名" allow-clear v-model="form.username">
        <a-icon slot="prefix" type="user" style="color: rgba(0,0,0,.25)" />
      </a-input>
    </a-form-model-item>
    <a-form-model-item prop="password">
      <a-input  @keyup.enter="onSubmit" type="password" placeholder="密码" allow-clear v-model="form.password">
        <a-icon slot="prefix" type="lock" style="color: rgba(0,0,0,.25)" />
      </a-input>
    </a-form-model-item>
    <a-form-model-item>
      <a-button type="primary" @click="onSubmit" block>登录</a-button>
      <a-button @click="resetForm" block>重置</a-button>
    </a-form-model-item>
    <message
      :isShow="msg.isShow"
      :type="msg.type"
      :message="msg.message"
      :description="msg.description"
      @closeMsg="closeMsg"
    ></message>
  </a-form-model>
</template>

<script>
import Message from "components/common/Message";

import { login } from "network/login";
import { SET_USER } from "store/mutation_types";

export default {
  name: "Login",
  components: {
    Message
  },
  data: () => ({
    msg: {
      isShow: false,
      type: "success",
      message: "登录成功",
      description: ""
    },
    form: {
      username: "",
      password: ""
    },
    rules: {
      username: [
        {
          required: true,
          message: "请输入用户名",
          trigger: "blur"
        }
      ],
      password: [
        {
          required: true,
          message: "请输入密码",
          trigger: "blur"
        }
      ]
    }
  }),
  methods: {
    onSubmit() {
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          login(this.form.username, this.form.password)
            .then(res => {
              if (res.data.flag) {
                window.localStorage["token"] = "token  " + res.data.data.token;
                this.$store.commit(SET_USER, {
                  user: {
                    id: res.data.data.userId,
                    username: res.data.data.username
                  }
                });
                this.msg.isShow = true;
                this.$router.push("/home");
              } else {
                this.msg.isShow = true;
                this.msg.message = "登陆失败";
                this.msg.type = "error";
                this.msg.description = res.data.error;
              }
            })
            .catch(err => {
              this.msg.isShow = true;
              this.msg.message = "登陆失败";
              this.msg.type = "error";
              this.msg.description = err;
              this.$router.push("/500");
            });
          setTimeout(() => {
            this.msg.isShow = false;
          }, 2000);
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    closeMsg() {
      this.msg.isShow = false;
    }
  }
};
</script>
<style>
.login-form {
  max-width: 700px;
  min-width: 500px;
  padding-left: 0;
  padding-right: 0;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (max-width: 800px) {
  .login-form {
    min-width: 300px;
  }
}
.login-form-button {
  width: 100%;
}
</style>