<template>
  <div class="msg" @click="closeMsg">
    <a-alert
      v-if="isShow"
      :message="message"
      :type="type"
      :description="description"
      show-icon
    />
  </div>
</template>

<script>
export default {
  name: "Message",
  props: {
    isShow: false,
    type: {
      type: String,
      default: "info"
    },
    message: {
      type: String,
      default: ""
    },
    description: {
      type: String,
      default: ""
    }
  },
  methods: {
    closeMsg() {
      this.$emit("closeMsg");
    }
  }
};
</script>

<style scoped>
.msg {
  position: fixed;
  min-width: 350px;
  left: 50%;
  top: 50%;
  z-index: 9;
  transform: translate(-50%, -50%);
}
</style>